nav{z-index:100;position:fixed;top:0;width:100%;height:100px;display:flex;align-items:center;justify-content:space-between;padding:0 5vw;background:#212121}nav .logo svg{height:70px;width:70px;transition:.1s ease-in}nav .logo svg:hover{transform:scale(1.1)}nav a{text-decoration:none;font-weight:600}nav .nav-links{display:flex;align-items:center;justify-content:space-between;counter-reset:link}nav .nav-links .nav-link{cursor:pointer;margin:0 20px;position:relative;color:#ccc;transition:.1s ease-in;z-index:100}nav .nav-links .nav-link:hover{color:#fff}nav .nav-links .nav-link:hover::before{color:#00e5ff}nav .nav-links .nav-link::before{counter-increment:link;content:"0" counter(link) ". ";font-family:'Fira Code', monospace;text-align:right;color:#00e5ff80;transition:color .2s ease-in}nav .nav-links .hamburger{height:30px;width:30px;display:flex;flex-direction:column;align-items:center;justify-content:space-evenly;cursor:pointer;margin:0 0 0 20px}nav .nav-links .hamburger.active{justify-content:center}nav .nav-links .hamburger.active>.line1{transform:rotate(-45deg) translateY(2.5px)}nav .nav-links .hamburger.active>.line2{opacity:0}nav .nav-links .hamburger.active>.line3{transform:rotate(45deg) translateY(-2.5px)}nav .nav-links .hamburger .line{height:2px;width:100%;background:#00e5ff;border-radius:20px;transition:.2s ease-out}nav .resume-btn{color:#00e5ff;border:2px solid #00e5ff;padding:10px;transition:.1s ease-in}nav .resume-btn:hover{background:#00e5ff20}.nav-link::after{content:'';height:2px;background:#fff;position:absolute;bottom:-5px;width:0%;left:0;transition:width .2s ease-in}.nav-link:hover::after{width:100%}@media only screen and (max-width: 768px){nav .nav-links .nav-link{display:none}nav .nav-links .hamburger{display:flex;height:30px;width:30px}}

.projects-section{min-height:100vh;flex:1 1}.projects-section .left{flex:.6 1;background:#2f3132;height:100%;width:100%;padding:30% 20px}.projects-section .right{flex:.4 1;height:100%;width:100%}.projects-section .project-list{display:grid;grid-template-columns:repeat(auto-fill, minmax(300px, 1fr));position:relative;grid-gap:15px;gap:15px}.projects-section .project-list li{list-style-type:none}.projects-section .project-list .project{box-shadow:0 0 15px 2px #00000020;border-radius:5px;background:#2f3132;cursor:pointer;transition:.1s ease-in;position:relative}.projects-section .project-list .project:hover{transform:translateY(-3px)}.projects-section .project-list .project .img-overlay{height:150px;width:100%;position:absolute;background:#00e5ff30}.projects-section .project-list .project img{height:150px;width:100%;object-fit:cover;object-position:center}.projects-section .project-list .project .info{z-index:100;padding:20px}.projects-section .project-list .project .info .project-title{font-size:1.2rem;font-weight:600;margin-bottom:20px}.projects-section .project-list .project .info .project-desc{font-family:'Fira Code', monospace;margin-bottom:20px;display:none}.projects-section .project-list .project .info .languages{display:flex}.projects-section .project-list .project .info .languages li{font-size:0.8rem;padding:5px 10px;background:#00000040;margin:0 10px}.projects-section .project-list .project .info .languages li:first-child{margin:0 10px 0 0}.projects-section .project-list .project .info .languages li:hover{background:#00000080}

.social-media-bar{position:fixed;height:100%;top:80px;right:-5vw;list-style-type:none;display:flex;flex-direction:column;align-items:center;justify-content:space-between;transition:.2s ease-out}.social-media-bar.active{right:5vw}.social-media-bar::after,.social-media-bar::before{content:'';background:#fff;height:100%;width:1px}.social-media-bar svg{width:20px;height:20px;margin:15px 0;fill:#fff;transition:.2s ease-in}.social-media-bar svg:hover{fill:#00e5ff;transform:translateY(-3px)}@media only screen and (max-width: 768px){.social-media-bar{display:none}}

.contact-bar {
	/* border: thin solid white; */
	text-decoration: none;
	position: fixed;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	letter-spacing: .5ch;
	font-size: 12px;
	font-family: 'Fira Code', monospace;
	-webkit-writing-mode: vertical-rl;
	    -ms-writing-mode: tb-rl;
	        writing-mode: vertical-rl;
	bottom: 0;
	right: 5vw;
	/* margin: 20px auto; */
	transition: .2s ease-in;
}

.contact-bar::after {
	content: "";
	display: inline-block;
	bottom: 0;
	/* position: absolute; */
	width: 1px;
	height: 90px;
	background: #fff;
	margin: 0px auto;
}

.contact-bar:hover {
	color: #00e5ff;
	transform: translateY(-3px);
}

