one;color:#ccc;font-size:0.95rem;font-weight:600;padding:5px 0}.experience-section .wrapper .inner-wrapper .info .card .desc-list li .sub-list{padding-left:20px}.experience-section .wrapper .inner-wrapper .info .card .desc-list li .sub-list li{font-size:.9rem;font-weight:100;font-family:'Fira Code', monospace}.experience-section .wrapper .inner-wrapper .info .card .desc-list li::before{content:"▹ ";position:relative;left:0px;color:#00e5ff}@media only screen and (max-width: 1024px){.experience-section .wrapper{padding:5vw}.experience-section .wrapper .inner-wrapper{display:grid;grid-template-columns:100%}.experience-section .wrapper .inner-wrapper .tab-slider{background:#21212190;margin-bottom:20px}.experience-section .wrapper .inner-wrapper .tab-slider .tab-indicator{top:0}.experience-section .wrapper .inner-wrapper .tab-slider .tab{width:100%}.experience-section .wrapper .inner-wrapper .tab-slider .tab svg{display:block;transition:.1s ease-in}.experience-section .wrapper .inner-wrapper .tab-slider .tab.active{border:thin solid #00e5ff}.experience-section .wrapper .inner-wrapper .tab-slider .tab.active svg{transform:rotate(180deg)}.experience-section .wrapper .inner-wrapper .tab-slider .tab+.info{display:none}.experience-section .wrapper .inner-wrapper .tab-slider .tab.active+.info{display:block}.experience-section .wrapper .inner-wrapper .tab-slider::before{display:none}.experience-section .wrapper .inner-wrapper>.info{display:none}}

