*{margin:0;padding:0;box-sizing:border-box;font-family:'Montserrat', sans-serif;color:#fff}:root{--section-padding-top: 100px;--section-height: 100vh}#root{width:100%;background:#212121;counter-reset:section}::-webkit-scrollbar{width:8px;background:#212121}::-webkit-scrollbar-thumb{background:#fff;border-radius:30px}section{position:relative;padding:100px 10vw;width:100%;min-height:100vh;display:flex;align-items:center;background:#212121}.title{font-weight:600;padding:20px 0;font-size:1.5em;position:relative}.title::before{counter-increment:section;content:"0" counter(section) ". ";color:#00e5ff}.title::after{content:'';background:#fff;display:block;height:1px;position:relative;margin:10px 0;max-width:30ch}

