.aside-menu {
	display: none; // Change This

	position: fixed;
	z-index: 99;
	height: calc(100vh - 100px);
	width: 100%;
	top: 100px;
	background: #212121;

	.links {
		position: relative;
		width: 100%;
		height: 80%;
		// border: thin solid #111;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: space-between;
		counter-reset: link;

		.nav-link-aside {
			width: 100%;
			text-align: center;
			padding: 2em;
			text-decoration: none;

			&::before {
				counter-increment: link;
				content: "0" counter(link) ".";
				font-family: 'Fira Code', monospace;
				text-align: right;
				color: #00e5ff;
				transition: color .2s ease-in;
			}

			&:hover {
				background: #ffffff10;
			}
		}
	}

	.social-media {
		position: relative;
		height: 20%;
		border: Thin solid red;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;
		list-style-type: none;

		&::before {
			content: '';
			height: 1px;
			background: #fff;
		}

		svg {
			width: 1.2em;
			height: 1.2em;
			margin: 0 1em;
			fill: #fff;
			transition: .1s ease-in;

			&::after,
			&::before {
				content: '';
			}

			&:hover {
				fill: #00e5ff;
				transform: translateY(-2px);
			}
		}
	}
}
